  }
}

.header__wrapper {
  display: flex;
  align-items: center;
}

.header__navigation {
  margin-left: auto;
}
@media (max-width: 767px) {
  .header__navigation {
    position: relative;
    display: none;
    flex-direction: column;
    background-color: #f9fbfd;
  }
}

.header__toggle {
  position: absolute;
  top: 25px;
  right: 20px;
  display: none;
  width: 26px;
  height: 12px;
  background: 0 0;
  border: 0;
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
  }
}

.header.is-active .header__logo {
  display: none;
}

.header.no-js .header__toggle {
  display: none;
}

.header.is-active .header__toggle {
  top: 25px;
  right: 8px;
}

.header.no-js .header__navigation,
.header.is-active .header__navigation {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  padding: 85px 19% 300px 4%;
  background-image: url("../img/svg/logo_menu.svg");
  background-repeat: no-repeat;
  background-position-x: 6%;
  background-position-y: 20px;
}

.header__open-icon {
  display: block;
}

.header__close-icon {
  display: none;
}

.header.is-active .header__open-icon {
  display: none;
}

.header.is-active .header__close-icon {
  display: block;
}

