.card {
  max-height: 386px;
  overflow: auto;
}

.card__image {
  width: 100%;
}

.card__upper-side {
  position: relative;
}

.card__upper-side h3 {
  position: absolute;
  bottom: 22px;
  left: 30px;

  margin: 0;
  padding: 0;

  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-alice-blue;

  @media (max-width: 1023px) {
    bottom: 27px;
    left: 35px;
  }

  @media (max-width: 767px) {
    bottom: 17px;
    left: 25px;
  }
}

.card__lower-side {
  display: none;
  width: 100%;
  height: 100%;
  padding: 29px 25px 35px 25px;

  color: $color-alice-blue;

  background-color: $color-tangaroa;

  @media (max-width: 1023px) {
    padding: 28px 35px 20px 35px;
  }

  @media (max-width: 767px) {
    padding: 29px 25px 50px 25px;
  }
}

.card__lower-side h3 {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;

  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.card__lower-side dl {
  margin: 0;
  padding: 0;
}

.card__lower-side dt {
  margin: 0;
  margin-bottom: 1px;
  padding: 0;

  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.card__lower-side dd {
  max-width: 210px;
  margin: 0;
  margin-bottom: 11px;
  padding: 0;

  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.card__btn {
  position: relative;
  bottom: -15px;

  width: 100%;
  padding: 9px 22%;

  font-weight: 600;
  font-size: 14px;
  line-height: 21px;

  @media (max-width: 1023px) {
    padding: 9px 25%;
  }

  @media (max-width: 767px) {
    padding: 9px 24%;
  }
}

.card:hover .card__upper-side {
  display: none;
}

.card:hover .card__lower-side {
  display: block;
}
